import { Component, OnInit, ViewChild, Inject} from '@angular/core';
import { routerTransition } from '../../router.animations';
import { FormControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, VERSION, MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ToasterService} from 'angular5-toaster';

@Component({
  selector: 'app-add-security',
  templateUrl: './add-security.component.html',
  styleUrls: ['./add-security.component.scss'],
  animations: [routerTransition()]

})
export class AddSecurityComponent implements OnInit {
  societyadmin = JSON.parse(localStorage['societyadmin']);
  response;

  displayedColumns = ['select', 'block', 'name', 'email', 'contact', 'actions'];
  dataSource: MatTableDataSource<Security>;
  selection = new SelectionModel<Security>(true, []);

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  security: Security[];
  private toasterService: ToasterService;

  constructor(
    private http: HttpClient,
    public dialog: MatDialog,
    toasterService: ToasterService,
    private _formBuilder: FormBuilder
  ) {
    this.toasterService = toasterService;

    this.getSecurityData();

   }

   getSecurityData(){
    this.http.get<Security[]>('api/securitylistdetails/' + this.societyadmin.id).subscribe(data=> {
      this.security = data;  
      console.log(this.security);
      this.dataSource = new MatTableDataSource(data);
     this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    });
  
  }
  openDialog(): void {
    let dialogRef = this.dialog.open(TestDialogComponent, {
      height: '350px',
      width: '620px',
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      if(result == null || result == ''){

      }

      else{
                  //Register Security
    this.http.post('api/addsecurity', result).subscribe(data => {
        this.response= data;
        if(this.response.success == true){
          this.toasterService.pop('success', this.response.message);
          this.getSecurityData();  

        }
        else{
          this.toasterService.pop('error', this.response.message);  

        }
    });
      }

    });
  }


  openEditDialog(security_id): void {
    console.log(security_id);
    let dialogRef = this.dialog.open(EditDialogComponent, {
      height: '360px',
      width: '260px',
      data: {security_id},
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      this.getSecurityData();
      if(result == null || result == ''){

      }

      else{

      }

    });
  }

  ngOnInit() {
    
  }


applyFilter(filterValue: string) {
  filterValue = filterValue.trim(); // Remove whitespace
  filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
  this.dataSource.filter = filterValue;
}
/** Whether the number of selected elements matches the total number of rows. */
isAllSelected() {
  const numSelected = this.selection.selected.length;
  const numRows = this.dataSource.data.length;
  return numSelected == numRows;
}

/** Selects all rows if they are not all selected; otherwise clear selection. */
masterToggle() {
  this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
}

}


//Dialog Box Component


@Component({
  template: `
  <div matDialogTitle>Security Registration</div>
  <mat-dialog-content>
  <form [formGroup]="firstFormGroup" >
  <ng-template matStepLabel>Fill out your Details</ng-template>
  <mat-form-field>
  <mat-select [(value)]="manager_id" placeholder="Select Manager" formControlName="managerCtrl" required>
  <mat-option *ngFor="let list of manager" [value]="list._id">{{list.manager_name}}</mat-option>
</mat-select>
<div *ngIf="managerCtrl.invalid && (managerCtrl.dirty || managerCtrl.touched)" class="text-danger">
<div *ngIf="managerCtrl.errors.required"><small>Required</small></div>
</div>
</mat-form-field>
<mat-form-field>
<input matInput [(ngModel)]="security_name" placeholder="Security Name" (keypress)="StringPress($event)"  formControlName="nameCtrl" required>
<div *ngIf="nameCtrl.invalid && (nameCtrl.dirty || nameCtrl.touched)" class="text-danger">
<div *ngIf="nameCtrl.errors.required"><small>Required</small></div>
</div>
</mat-form-field>

<mat-form-field>
<input matInput [(ngModel)]="security_contact" #contactinput placeholder="Contact" (keypress)="NumPress($event)" formControlName="contactCtrl" minlength="10" maxlength="10" required>
<mat-hint align="end">{{contactinput.value?.length || 0}}/10</mat-hint>
<div *ngIf="contactCtrl.invalid && (contactCtrl.dirty || contactCtrl.touched)" class="text-danger">
<div *ngIf="contactCtrl.errors.required"><small>Required</small></div>    
<div *ngIf="contactCtrl.errors.minlength"><small>Should be 10 Digit</small></div>
<div *ngIf="contactCtrl.errors.maxlength"><small>Should be 10 Digit</small></div>
</div>
</mat-form-field>

<mat-form-field>
<input matInput [(ngModel)]="security_email" placeholder="Email" formControlName="emailCtrl" required>
<div *ngIf="emailCtrl.invalid && (emailCtrl.dirty || emailCtrl.touched)" class="text-danger">
<div *ngIf="emailCtrl.errors.required"><small>Required</small></div>
<div *ngIf="emailCtrl.dirty" class="text-danger">        
<div *ngIf="emailCtrl.errors.email"><small>Invalid</small></div>
</div>
</div>

</mat-form-field>

 
<mat-form-field>
<input matInput type="password" [(ngModel)]="security_password"  #passwordinput placeholder="Password" formControlName="passwordCtrl" minlength="6" maxlength="12" required>
<mat-hint align="end">{{passwordinput.value?.length || 0}}/[Min 6 -Max 12]</mat-hint>
<div *ngIf="passwordCtrl.invalid && (passwordCtrl.dirty || passwordCtrl.touched)" class="text-danger">
<div *ngIf="passwordCtrl.errors.required"><small>Required</small></div>
<div *ngIf="passwordCtrl.errors.minlength"><small>At least 6 characters long</small></div>
<div *ngIf="passwordCtrl.errors.maxlength"><small>Must not exceed 12 characters</small></div>
</div>   
</mat-form-field>

<mat-form-field>
<input matInput type="password" name="confirm_password" [(ngModel)]="security_confirm_password" pattern={{this.security_password}} #confirmpasswrdinput placeholder="Confirm Password" formControlName="confirmpasswordCtrl" minlength="6" maxlength="12" required>
<mat-hint align="end">{{confirmpasswrdinput.value?.length || 0}}</mat-hint>
<div *ngIf="confirmpasswordCtrl.invalid && (confirmpasswordCtrl.dirty || confirmpasswordCtrl.touched)" class="text-danger">
<div *ngIf="confirmpasswordCtrl.errors.required"><small>Re-Enter the Password</small></div>
<div *ngIf="confirmpasswordCtrl.errors.pattern"><small>Password is not match</small></div>
</div>  
</mat-form-field>

</form>
  </mat-dialog-content>
  <mat-dialog-actions class="pull-right">
    <button class="btn btn-danger buttoncolor" style="color:white;" mat-button matDialogClose>Cancle</button>
        <button type="button" class="btn btn-success buttoncolor" (click)="onSubmitClick()" style="color:white;"mat-button [disabled]="!firstFormGroup.valid" >Submit</button>

  </mat-dialog-actions>`,

})
export class TestDialogComponent implements OnInit {

  societyadmin = JSON.parse(localStorage['societyadmin']);

  manager: Manager[];
  manager_id: string;

  charsetOptions: any
  firstFormGroup: FormGroup;

  security_name: string;
  security_email : string;
  security_contact: number;
  security_password: string;
  security_confirm_password : string;

  constructor(
    private http: HttpClient,
    private _formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<AddSecurityComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any

        
 
  ) {
    this.firstFormGroup = this._formBuilder.group({
      managerCtrl: ['', Validators.required],
      nameCtrl: ['', Validators.required],
      emailCtrl: ['', [Validators.required, Validators.email]],
      contactCtrl:  ['', Validators.required],
      passwordCtrl: ['', [ Validators.required, Validators.minLength(6), Validators.maxLength(12)]],
      confirmpasswordCtrl: ['', Validators.required]
    });

  }
  get managerCtrl() { return this.firstFormGroup.get('managerCtrl'); }
  get nameCtrl() { return this.firstFormGroup.get('nameCtrl'); }
  get emailCtrl() { return this.firstFormGroup.get('emailCtrl'); }
  get contactCtrl() { return this.firstFormGroup.get('contactCtrl'); }
  get passwordCtrl() { return this.firstFormGroup.get('passwordCtrl'); }
  get confirmpasswordCtrl() { return this.firstFormGroup.get('confirmpasswordCtrl'); }




  ngOnInit() {
    this.http.get<Manager[]>('api/managerlistdetails/' + this.societyadmin.id).subscribe(data => {
      this.manager =  data;
      console.log(this.manager);
    });
  }

    //Contact Validation
NumPress(event: any) {
  const pattern = /[0-9\+\-\ ]/;

  let inputChar = String.fromCharCode(event.charCode);
  if (event.keyCode != 8 && !pattern.test(inputChar)) {
    event.preventDefault();

  }
}
       //Only String (Name)
       StringPress(event: any) {
        const pattern = /[a-z\+\A-Z\+\-\ ]/;
    
        let inputChar = String.fromCharCode(event.charCode);
        if (event.keyCode != 8 && !pattern.test(inputChar)) {
          event.preventDefault();
        }
      }

      onSubmitClick(): void {
        console.log(this.manager_id);
        const security = {
          society_id: this.societyadmin.id,
          manager_id: this.manager_id,
          security_name: this.security_name,
          email : this.security_email,
          contact: this.security_contact,
          password : this.security_confirm_password
        }
        
        this.dialogRef.close(security);
      }

}




